import React, { Component } from 'react';
import './Dropdown.css';

import DropItem from '../DropItem/DropItem';

class Dropdown extends Component {
  state = {
    items: this.props.items,
    currentItem: this.props.currentItem,
    isDropdownOpen: this.props.isDropdownOpen,
  };

  componentDidUpdate() {
    if (this.props.currentItem !== this.state.currentItem) {
      this.setState({
        currentItem: this.props.currentItem,
      });
    }

    if (this.state.isDropdownOpen !== this.props.isDropdownOpen) {
      this.setState({
        isDropdownOpen: this.props.isDropdownOpen,
      });
    }
  }

  onDropClick = (e) => {
    this.setDropdownOpen(!this.state.isDropdownOpen);
  };

  setDropdownOpen = (isOpen) => {
    this.props.setDropdownOpen(isOpen);
  };

  changeItem = (item) => {
    this.props.changeItem(item);
    this.props.setDropdownOpen(false);
  };

  render() {
    return (
      <div className="select-container" style={this.props.formStyle}>
        <button
          id="dropbtn"
          className="form-select custom-select"
          onClick={this.onDropClick}
          type="button"
          style={this.props.btnStyle}
        >
          {this.state.currentItem}
        </button>
        <ul
          id="droplist"
          className="form-dropdownlist"
          style={this.state.isDropdownOpen ? { display: 'block' } : { display: 'none' }}
        >
          {this.state.items.map((item, i) => (
            <DropItem
              key={i}
              item={item}
              changeItem={this.changeItem}
              currentItem={this.state.currentItem}
              setDropdownOpen={this.props.setDropdownOpen}
            />
          ))}
        </ul>
      </div>
    );
  }
}

export default Dropdown;
