import React, { Component } from 'react';
import './Audio.css';

class Audio extends Component {
  constructor(props) {
    super(props);
    this.state = {
      play: false,
      isMuted: false,
    };
    this.audio = null;
  }

  componentDidMount = () => {
    //setTimeout(() => this.playAudio(), 5000);
  };

  componentDidUpdate = () => {
    /*if (this.props.isFinalState) {
      if (!this.state.play) {
        this.setState({
          play: true,
        });
        this.playAudio();
      }
    }*/

    if (this.props.startWasClicked) {
      if (!this.state.play) {
        this.setState({
          play: true,
        });
        this.playAudio();
      }
    }
  };

  playAudio = async () => {
    this.audio = document.getElementById('audio-id');
    try {
      await this.audio.play();
    } catch (err) {
      console.log(err);
    }
  };

  toggleAudio = (e) => {
    this.setState({
      isMuted: !this.state.isMuted,
    });
  };

  render() {
    return (
      <React.Fragment>
        <div
          className="sound-container"
          onClick={this.toggleAudio}          
        >
          <img className="sound-icon" src="images/sound-icon.png" alt='Mute Music'></img>
          <div
            className="sound-off"
            style={this.state.isMuted ? { opacity: '1' } : { opacity: '0' }}
          ></div>
        </div>

        <audio id="audio-id" loop={true} muted={this.state.isMuted}>
          <source src="/audio/Jungle_track.mp3" type="audio/mpeg" />
          Your browser does not support the audio element.
        </audio>
      </React.Fragment>
    );
  }
}

export default Audio;
