import React from 'react';
import './ProgressText.css';

const ProgressText = (props) => {
  return (
    <div
      className="progress-container"
      style={props.isVisible ? { opacity: 0.6 } : { opacity: 0 }}
    >
      <h4 className="progress-txt">{props.progress + '%'}</h4>
    </div>
  );
};

export default ProgressText;
