import React from 'react';
import './DotItem.css';

const DotItem = (props) => {
  return (
    <li
      className="section-dot"
      style={
        props.section === props.currentSection
          ? { background: 'white' }
          : { background: 'transparent' }
      }
    ></li>
  );
};

export default DotItem;
