import React, { Component } from 'react';
import './DropItem.css';

class DropItem extends Component {
  onItemClick = (e) => {
    this.props.changeItem(this.props.item);
  };

  render() {
    return (
      <li
        className="dropitem"
        onClick={this.onItemClick}
        style={
          this.props.currentItem === this.props.item ? { backgroundColor: 'gray' } : {}
        }
      >
        {this.props.item}
      </li>
    );
  }
  /*
  onItemClick = (e) => {
    this.props.changeCountry(this.props.country);
  };

  render() {
    return (
      <li
        className="dropitem"
        onClick={this.onItemClick}
        style={
          this.props.currentCountry === this.props.country
            ? { backgroundColor: 'gray' }
            : {}
        }
      >
        {this.props.country}
      </li>
    );
  }*/
}

export default DropItem;
