import React, { Component } from 'react';
import './PressStart.css';

import anime from 'animejs';

const startStyle = {
  start: {
    transform: 'translate(0%, 60%)',
    opacity: '0',
    transition: 'transform 0.4s ease-in, opacity 0.4s ease-in',
    pointerEvents: 'none',
  },
  final: {
    transform: 'translate(0%, 0%)',
    opacity: '1',
    transition: 'transform 0.4s ease-out, opacity 0.4s ease-out',
    pointerEvents: 'none',
  },
};

const circleStyle = {
  start: {
    width: '55px',
    height: '55px',
    opacity: '0',
    transition: 'width 0.5s ease-in-out, height 0.5s ease-in-out',
    pointerEvents: 'auto',
  },
  final: {
    width: '65px',
    height: '65px',
    opacity: '1',
    transition: 'width 0.5s ease-in-out, height 0.5s ease-in-out',
    pointerEvents: 'auto',
  },
  invisible: {
    width: '65px',
    height: '65px',
    opacity: '0',
    transition: 'opacity 1s ease-in-out',
    pointerEvents: 'none',
  },
};

class PressStart extends Component {
  state = {
    currentSection: '',
    isVisible: false,
    isStartVisible: false,
    currentStartStyle: {},
    currentCircleStyle: {},

    showScroll: true,
    isHidden: false,
    isHovered: false,

    wasClicked: false,

    width: this.props.width,
    isMobile: false,
  };

  componentDidMount() {
    this.setState({
      currentStartStyle: startStyle.start,
      currentCircleStyle: circleStyle.invisible,
    });

    this.hideScrollToNavigate();
  }

  componentDidUpdate() {
    if (this.props.width <= 550) {
      if (!this.state.isMobile) {
        this.setState({
          isMobile: true,
        });
      }
    } else {
      if (this.state.isMobile) {
        this.setState({
          isMobile: false,
        });
      }
    }
    if (this.state.currentSection !== this.props.currentSection) {
      this.setState({
        currentSection: this.props.currentSection,
      });
      if (this.props.section === this.props.currentSection) {
        this.setState({
          isVisible: true,
        });
      } else {
        this.setState({
          isVisible: false,
        });

        if (this.props.currentSection === 'Home' && this.state.showScroll) {
          this.showScrollToNavigate();
          this.setState({
            showScroll: false,
          });
        }

        if (this.props.currentSection !== 'Home' && !this.state.isHidden) {
          this.hideScrollToNavigate();
          this.setState({
            isHidden: true,
          });
        }
      }
    }

    if (this.state.isHovered !== this.props.isStartHovered) {
      this.setState({
        isHovered: this.props.isStartHovered,
      });
    }
  }

  getBTNStyle = () => {
    let BTNStyle = {};

    if (this.state.isVisible) {
      if (this.props.isStartHovered) {
        BTNStyle = circleStyle.start;
      } else {
        BTNStyle = circleStyle.final;
      }
    } else {
      BTNStyle = circleStyle.invisible;
    }

    return BTNStyle;
  };

  getStartStyle = () => {
    if (this.state.isMobile) {
      if (this.state.isVisible) {
        return startStyle.final;
      } else {
        return startStyle.start;
      }
    }
    return this.state.currentStartStyle;
  };

  onMouseEnter = (e) => {
    this.setState({
      currentStartStyle: startStyle.final,
    });
    this.props.setIsStartHovered(true);
  };

  onMouseLeave = (e) => {
    this.setState({
      currentStartStyle: startStyle.start,
    });
    this.props.setIsStartHovered(false);
  };

  onClick = async (e) => {
    this.setState({
      isVisible: false,
      currentStartStyle: startStyle.start,
    });
    this.props.setStartWasClicked(true);
    this.props.setIsStartHovered(false);
    await this.props.setHasAnimFinished(true);
    this.props.goForwardSection();
  };

  showScrollToNavigate = () => {
    if (!this.state.isMobile) {
      anime.timeline({ loop: false }).add({
        targets: '.start-btn-scroll-txt',
        translateY: 0,
        opacity: 1,
        duration: 300,
        delay: 5000,
        easing: 'easeInOutQuad',
      });
    }
  };

  hideScrollToNavigate = () => {
    anime({
      targets: '.start-btn-scroll-txt',
      translateY: 30,
      opacity: 0,
      duration: 500,
      easing: 'easeInOutQuad',
    });
  };

  render() {
    return (
      <div>
        <div className="press-start">
          <button
            className="start-btn"
            style={this.getBTNStyle()}
            onMouseEnter={this.onMouseEnter}
            onMouseLeave={this.onMouseLeave}
            onClick={this.onClick}
          ></button>
        </div>
        <div className="press-start-txt">
          <h1 className="start-btn-txt" style={this.getStartStyle()}>
            Start
          </h1>
        </div>
        <div className="press-start-scroll">
          <h2 className="start-btn-scroll-txt">Scroll to navigate</h2>
        </div>
      </div>
    );
  }
}

export default PressStart;
