import React, { Component } from 'react';
import './App.css';

import Opening from './components/Opening/Opening';

class App extends Component {
  state = {
    sections: ['Start', 'Home', 'About', 'Mission', 'Join'],
    sectionIndex: 0,
    section: 'Start',
    canScroll: true,
    width: 0,
    height: 0,
  };

  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }

  componentWillUnmount() {
    document.removeEventListener('wheel', this.handleScroll);
    window.removeEventListener('resize', this.updateWindowDimensions);
  }

  // componentWillUnmount() {
    
  // }

  updateWindowDimensions = () => {
    this.setState({ width: window.innerWidth, height: window.innerHeight });
  };

  startScrollListener = (container) => {
    container.addEventListener('wheel', this.handleScroll);
  };

  handleScroll = (e) => {
    if (this.state.sections[this.state.sectionIndex] !== 'Start') {
      let direction = e.deltaY / Math.abs(e.deltaY);
      if (direction > 0) {
        this.goForward();
      } else if (direction < 0) {
        this.goBackward();
      }
    }
  };

  goForward = () => {
    if (this.state.canScroll) {
      if (this.state.sectionIndex + 1 < this.state.sections.length) {
        this.setSectionIndex(this.state.sectionIndex + 1);
      }
    }
  };

  goBackward = () => {
    if (this.state.canScroll) {
      if (this.state.sections[this.state.sectionIndex - 1] !== 'Start') {
        if (this.state.sectionIndex - 1 >= 0) {
          this.setSectionIndex(this.state.sectionIndex - 1);
        }
      }
    }
  };

  setSectionIndex = (sectionIndex) => {
    this.setState({
      sectionIndex,
    });
  };

  setCanScroll = (canScroll) => {
    this.setState({
      canScroll,
    });
  };

  changeSection = (section) => {
    this.setState({ section });
  };

  render() {
    return (
      <div className="app">
        <Opening
          changeSection={this.changeSection}
          sections={this.state.sections}
          section={this.state.sections[this.state.sectionIndex]}
          setCanScroll={this.setCanScroll}
          goForwardSection={this.goForward}
          goBackwardSection={this.goBackward}
          startScrollListener={this.startScrollListener}
          width={this.state.width}
        />
      </div>
    );
  }
}

export default App;
