import React, { Component } from 'react';
import './Logo.css';

import DotItem from '../DotItem/DotItem';

class Logo extends Component {
  state = {
    logoMini: false,
    logoPosition: {
      top: '50%',
      left: '50%',
      transform: 'translate(-50%,-50%)',
    },
    logoStyle: {
      height: '210px',
      width: '190px',
      transition: 'none',
      filter: 'none',
      opacity: '0.4',
    },
    isMouseOnTopOfLogoWhileLoading: false,
  };

  getLogoStyle = () => {
    return {
      height: '85px',
      width: '78px',
      filter: 'brightness(0) invert(1)',
      opacity: '1',
    };
  };

  render() {
    return (
      <div className="opening-logo">
        <img
          id="logo-opening"
          className="logo-hover"
          alt="logo"
          src="/images/LOGO_SUPERFOREST.png"
          style={this.getLogoStyle()}
        ></img>
        <ul className="section-list-dot">
          <DotItem section="Start" currentSection={this.props.section} />
          <DotItem section="Home" currentSection={this.props.section} />
          <DotItem section="About" currentSection={this.props.section} />
          <DotItem section="Mission" currentSection={this.props.section} />
          <DotItem section="Join" currentSection={this.props.section} />
        </ul>
      </div>
    );
  }
}

export default Logo;
