import React, { Component } from 'react';
import './AnimatedText3.css';

import Radium from 'radium';
import anime from 'animejs';

const Word = (props) => {
  const letters = [];
  switch (props.type) {
    case 'title':
      props.word.split('').forEach((l, i) =>
        letters.push(
          <span className={props.lStyle} key={i}>
            {l}
          </span>,
        ),
      );
      return <h1 className={props.wStyle}>{letters}</h1>;
    case 'paragraph':
      props.word.split('').forEach((l, i) =>
        letters.push(
          <span className={props.lStyle} key={i}>
            {l}
          </span>,
        ),
      );
      return <span className={props.wStyle}>{letters}</span>;
    default:
      props.word.split('').forEach((l, i) =>
        letters.push(
          <span className={props.lStyle} key={i}>
            {l}
          </span>,
        ),
      );
      return <h1 className={props.wStyle}>{letters}</h1>;
  }
};

// Title Settings (in ms)
const delayTitlePerWord_SHOW = 40; // Delay applied per word (1) 40ms, word (2) 80ms, etc. When the text appears.
const titleAnimPerWordDuration_SHOW = 1200; // Duration of animation when each letter appears
const delayTitlePerWord_HIDE = 30; // Delay applied per word. When the text disappears.
const titleAnimPerWordDuration_HIDE = 900; // Duration of animation when each letter dissappear

// Line Settings (in ms)
const delayLine_SHOW = 2000; // Delay applied after the title appears
const lineAnimDuration_SHOW = 1200; // Duration of animation when line appears
const delayLine_HIDE = 100; // Delay applied for hiding the line
const lineAnimDuration_HIDE = 600; // // Duration of animation when line disappears

// Content Settings (in ms)
const delayContent_SHOW = 1250; // Delay applied after the line appears
const delayContentPerWord_SHOW = 5; // Delay applied per word. When the content appears
const contentAnimPerWordDuration_SHOW = 500; // Duration of animation when each letter appears
const delayContent_HIDE = 50; // Delay applied when the content disappears
const delayContentPerWord_HIDE = 5; // Delay applied per word. When the content disappears
const contentAnimPerWordDuration_HIDE = 300; // Duration of animation when each letter dissappear

var currentTitleAnim = null;
var currentLineAnim = null;
var currentContentAnim = null;
class AnimatedText3 extends Component {
  state = {
    currentSection: '',
    stage: 'HIDE',

    initialDelay: this.props.initialDelay,
  };

  componentDidMount = () => {};

  componentDidUpdate = () => {
    if (this.state.initialDelay !== this.props.initialDelay) {
      this.setState({
        initialDelay: this.props.initialDelay,
      });
    }

    if (this.state.currentSection !== this.props.currentSection) {
      this.setState({
        currentSection: this.props.currentSection,
      });

      if (this.props.section === this.props.currentSection) {
        this.showTitle();
        this.showLine();
        this.showContent();
        this.setState({
          stage: 'SHOW',
        });
      } else if (this.state.stage !== 'HIDE') {
        this.hideTitle();
        this.hideLine();
        this.hideContent();

        this.setState({ stage: 'HIDE' });
      }
    }
  };

  showTitle = () => {
    let initialDelay = this.state.initialDelay;
    let delayTitlePerWord = delayTitlePerWord_SHOW;

    let duration = titleAnimPerWordDuration_SHOW;

    currentTitleAnim = anime({
      targets: `.title-key-${this.props.section}`,
      translateX: [40, 0],
      translateZ: 0,
      opacity: [0, 1],
      easing: 'easeOutExpo',
      duration: duration,
      delay: (el, i) => initialDelay + delayTitlePerWord * i,
    });
    currentTitleAnim.play();
  };

  hideTitle = () => {
    let initialDelay = 100;
    let delayTitlePerWord = delayTitlePerWord_HIDE;

    let duration = titleAnimPerWordDuration_HIDE;

    currentTitleAnim = anime({
      targets: `.title-key-${this.props.section}`,
      translateX: [0, -30],
      opacity: [1, 0],
      easing: 'easeInExpo',
      duration: duration,
      delay: (el, i) => initialDelay + delayTitlePerWord * i,
    });

    currentTitleAnim.play();
  };

  getTitle = () => {
    const words = this.props.title.split(' ');
    const title = [];

    words.forEach((word, i) => {
      title.push(
        <Word
          key={i}
          type={'title'}
          word={word}
          wStyle={`w-title title-key-${this.props.section}`}
          lStyle={`l-title title-key-${this.props.section}`}
        />,
      );
    });

    return title;
  };

  showLine = () => {
    let titleDelay = delayTitlePerWord_SHOW * this.props.title.split(' ').length;
    let initialDelay = this.state.initialDelay + titleDelay + delayLine_SHOW;

    let duration = lineAnimDuration_SHOW;

    currentLineAnim = anime({
      targets: `.line-key-${this.props.section}`,
      translateX: [40, 0],
      translateZ: 0,
      opacity: [0, 1],
      easing: 'easeOutExpo',
      duration: duration,
      delay: initialDelay,
    });

    currentLineAnim.play();
  };

  hideLine = () => {
    let initialDelay = delayLine_HIDE;

    let duration = lineAnimDuration_HIDE;

    currentLineAnim = anime({
      targets: `.line-key-${this.props.section}`,
      translateX: [0, -30],
      opacity: [1, 0],
      easing: 'easeInExpo',
      duration: duration,
      delay: initialDelay,
    });

    currentLineAnim.play();
  };

  showContent = () => {
    let titleDelay = delayTitlePerWord_SHOW * this.props.title.split(' ').length;
    let lineDelay = delayLine_SHOW;

    let initialDelay =
      this.state.initialDelay + titleDelay + lineDelay + delayContent_SHOW;
    let delayContentPerWord = delayContentPerWord_SHOW;

    let duration = contentAnimPerWordDuration_SHOW;

    currentContentAnim = anime({
      targets: `.content-key-${this.props.section}`,
      translateX: [40, 0],
      translateZ: 0,
      opacity: [0, 1],
      easing: 'easeOutExpo',
      duration: duration,
      delay: (el, i) => initialDelay + delayContentPerWord * i,
      complete: (anim) => this.props.setHasAnimFinished(true),
    });

    currentContentAnim.play();
  };

  hideContent = () => {
    let initialDelay = delayContent_HIDE;
    let delayContentPerWord = delayContentPerWord_HIDE;

    let duration = contentAnimPerWordDuration_HIDE;

    currentContentAnim = anime({
      targets: `.content-key-${this.props.section}`,
      translateX: [0, -30],
      opacity: [1, 0],
      easing: 'easeInExpo',
      duration: duration,
      delay: (el, i) => initialDelay + delayContentPerWord * i,
    });

    currentContentAnim.play();
  };

  getContent = () => {
    const words = this.props.content.split(' ');
    const content = [];

    words.forEach((word, i) => {
      content.push(
        <React.Fragment key={i}>
          <Word
            type="paragraph"
            word={word}
            wStyle={`w-content content-key-${this.props.section}`}
            lStyle={`l-content content-key-${this.props.section}`}
          />
          <span> </span>
        </React.Fragment>,
      );
    });

    let style =
      this.state.stage === 'SHOW'
        ? {
            textAlign: 'justify',
            pointerEvents: 'auto',
          }
        : {
            textAlign: 'justify',
            pointerEvents: 'none',
          };
    return (
      <p className="content-holder" style={style}>
        {content}
      </p>
    );
  };

  getLine = () => {
    return <div className={`th-line line-key-${this.props.section}`}></div>;
  };

  render() {
    return (
      <div className="textholder-3 th-perspective" style={this.props.parentStyle}>
        {this.getTitle()}
        {this.getLine()}
        {this.getContent()}
      </div>
    );
  }
}

export default Radium(AnimatedText3);
