import React, { Component } from 'react';
import './Universe.css';

import { World } from './World/World';

class Universe extends Component {
  constructor(props) {
    super(props);
    this.container = null;
    this.world = null;
    this.state = {
      section: '',
      progress: this.props.progress,
      updateProgress: false,
      isFinalState: this.props.isFinalState,
    };
  }

  componentDidMount() {
    this.init();
  }

  componentWillUnmount() {
    if (this.world !== null) {
      this.world.stopListeners();
    }
  }

  componentDidUpdate() {
    if (this.props.section !== this.state.section) {
      this.setState({
        section: this.props.section,
      });
      if (this.world !== null) {
        this.world.setSection(this.props.section);
      }
      this.changeScene(this.props.section);
    }

    if (this.state.isFinalState !== this.props.isFinalState) {
      this.setState({
        isFinalState: this.props.isFinalState,
      });
      this.props.startScrollListener(this.container);
    }

    if (this.state.progress !== this.props.progress) {
      this.setState({
        progress: this.props.progress,
      });
    }

    if (this.state.updateProgress && this.props.isLoading) {
      this.setState({
        updateProgress: false,
      });

      let worldProgress = this.world.getProgress();
      if (worldProgress >= 99) {
        setTimeout(() => {
          this.props.setLoadingFinalState(100, false, false);
        }, 1000);
      }
    }
  }

  init = async () => {
    const container = document.querySelector('#universe');
    this.container = container;
    this.world = new World(
      this.container,
      this.callbackProgress,
      this.props.setCanScroll,
      this.state.section,
      this.props.setCircleStyle,
      this.props.goForwardSection,
      this.props.goBackwardSection,
    );

    this.world.startListeners();

    await this.world.init();
    this.world.showScene();
    this.world.start();

    this.world.setStart();
  };

  callbackProgress = () => {
    let worldProgress = this.world.getProgress();
    if (this.state.progress !== worldProgress) {
      this.props.setProgress(worldProgress);
      this.setState({ updateProgress: true });
    }
  };

  changeScene = (section) => {
    this.props.setCanScroll(false);
    switch (section) {
      case 'Start':
        //this.world.setStart();
        break;
      case 'Home':
        this.world.setHome();
        break;
      case 'Mission':
        this.world.setMission();
        break;
      case 'About':
        this.world.setAbout();
        break;
      case 'Follow':
        this.world.setFollow();
        break;
      case 'Test':
        this.world.setTest();
        break;
      case 'Join':
        this.world.setJoin();
        break;
      default:
        console.log('nothing');
    }
  };

  render() {
    return <div id="universe"></div>;
  }
}

export default Universe;
