import React, { Component } from 'react';
import './Circle.css';

import anime from 'animejs';

class Circle extends Component {
  constructor(props) {
    super();
    this.delay = null;
    this.currentAnimation = null;
    this.state = {
      currentCircleStyle: {},
      stage: 'Start',
      isStartHovered: false,
    };
  }

  componentDidMount() {
    this.setState({
      currentCircleStyle: this.props.circleStyle,
      isStartHovered: this.props.isStartHovered,
    });
  }

  componentDidUpdate() {
    if (this.state.isStartHovered !== this.props.isStartHovered) {
      this.setState({
        isStartHovered: this.props.isStartHovered,
      });
      if (this.props.isStartHovered) {
        this.inside();
      } else {
        this.outside();
      }
    }
  }

  inside = () => {
    if (this.currentAnimation) this.currentAnimation.pause();
    this.currentAnimation = anime
      .timeline({ loop: false })
      .add({
        targets: '#circle',
        width: 50,
        height: 50,
        duration: 100,
        easing: 'linear',
      })
      .add({
        targets: '#circle',
        width: 65,
        height: 65,
        duration: 300,
        easing: 'linear',
      });

    this.currentAnimation.play();
  };

  outside = () => {
    if (this.currentAnimation) this.currentAnimation.pause();
    this.currentAnimation = anime({
      targets: '#circle',
      width: 25,
      height: 25,
      duration: 0,
      easing: 'linear',
      loop: false,
    });

    this.currentAnimation.play();
  };

  render() {
    return (
      <button
        id="circle"
        style={
          this.state.isStartHovered ? { top: '50%', left: '50%' } : this.props.circleStyle
        }
      ></button>
    );
  }
}

export default Circle;
