import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader.js';
import {
  TextureLoader,
  MeshStandardMaterial,
  sRGBEncoding,
  MathUtils,
  AnimationMixer,
  LoopRepeat,
  LoadingManager,
} from 'three';

const loadDeer = async (pl) => {
  const manager = new LoadingManager();
  const loader = new FBXLoader(manager);

  const deerPath = '/assets/deer/DEER_idle_3_200.fbx';
  manager.onStart = (url, itemsLoaded, itemsTotal) => {
    // pl.updateProgress('deer', 0);
  };

  manager.onProgress = (item, loaded, total) => {
    if (item === deerPath) pl.updateProgress('deer', (loaded / total) * 100);
  };

  manager.onLoad = () => {
    pl.updateProgress('deer', 100);
  };

  const deer = await loader.loadAsync(deerPath);

  deer.position.set(-15, -0.8, -3);
  deer.rotation.y = MathUtils.degToRad(240);
  deer.scale.multiplyScalar(0.009);

  const clip = deer.animations[0];
  const mixer = new AnimationMixer(deer);
  const action = mixer.clipAction(clip);
  action.setLoop(LoopRepeat);
  action.play();

  deer.tick = (delta) => mixer.update(delta);

  const textureLoader = new TextureLoader();
  const T_DEER_00_01_A = textureLoader.load('/assets/deer/textures/T_DEER_00_01_A.jpg');
  T_DEER_00_01_A.encoding = sRGBEncoding;
  deer.children[1].material = new MeshStandardMaterial({
    map: T_DEER_00_01_A,
    skinning: true,
  });
  return { deer };
};

export { loadDeer };
