import React, { Component } from 'react';
import './LogoVideo.css';

import Logo from './Logo_light.mp4';

class LogoVideo extends Component {
  componentDidMount() {}

  onEndedVideo = () => {
    this.props.setLogoVideoEnded(true);
  };
  onClick = (e) => {
    if (!this.props.isLoading) {
      this.props.setLogoVideoEnded(true);
      console.log('Oprimir logo video');
    }
  };

  render() {
    return (
      <div className="logo-wrapper">
        <video
          className="logo-video"
          autoPlay
          playsInline
          muted
          onEnded={this.onEndedVideo}
          style={
            !this.props.isFinalState
              ? { display: 'block', opacity: 1, pointerEvents: 'auto' }
              : { display: 'none', opacity: 0, pointerEvents: 'none' }
          }
          onClick={this.onClick}
          onTouchStart={this.onClick}
        >
          <source src={Logo} type="video/mp4" />
        </video>
      </div>
    );
  }
}

export default LogoVideo;
