import React, { Component } from 'react';
import Radium from 'radium';
import './TextHolder.css';

import AnimatedForm from '../AnimatedForm/AnimatedForm';
import AnimatedText3 from '../AnimatedText3/AnimatedText3';
import AnimatedLink from '../AnimatedLink/AnimatedLink';
import PressStart from '../PressStart/PressStart';

class TextHolder extends Component {
  state = {
    prevSection: '',
    section: '',

    hasAnimFinished: true,
  };

  setHasAnimFinished = (hasAnimFinished) => {
    this.setState({
      hasAnimFinished,
    });
  };

  componentDidUpdate = () => {
    //if (this.props.isFinalState) {

    if (this.state.section !== this.props.section) {
      this.setState({
        hasAnimFinished: false,
        prevSection: this.props.prevSection,
        section: this.props.section,
      });
    }

    if (this.state.hasAnimFinished) {
      this.props.setCanScroll(true);
      this.setState({
        hasAnimFinished: false,
      });
    }
    //}

    if (this.state.isDropdownOpen !== this.props.isDropdownOpen) {
      this.setState({
        isDropdownOpen: this.props.isDropdownOpen,
      });
    }
  };

  render() {
    return (
      <React.Fragment>
        <PressStart
          section="Start"
          currentSection={this.state.section}
          goForwardSection={this.props.goForwardSection}
          setIsStartHovered={this.props.setIsStartHovered}
          isStartHovered={this.props.isStartHovered}
          setHasAnimFinished={this.setHasAnimFinished}
          width={this.props.width}
          setStartWasClicked={this.props.setStartWasClicked}
        />

        <AnimatedText3
          section="Home"
          setHasAnimFinished={this.setHasAnimFinished}
          currentSection={this.state.section}
          prevSection={this.state.prevSection}
          initialDelay={5000}
          title="Gamified climate action platform"
          content="Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat. Ut wisi enim ad minim veniam, quis nostrud exerci tation ullamcor- per suscipit lobortis nisl ut."
          parentStyle={{
            top: '50%',
            left: '0%',
            right: 'initial',
            //transform: 'translate(0%,-50%)',
            paddingLeft: '2rem',
            paddingRight: '2rem',
            '@media (min-width: 550px)': {
              //transform: 'translate(0%,-50%)',
              minWidth: 'auto',
              maxWidth: '400px',
              paddingLeft: '4rem',
              paddingRight: '4rem',
            },
          }}
        />

        <AnimatedText3
          section="About"
          currentSection={this.state.section}
          setHasAnimFinished={this.setHasAnimFinished}
          prevSection={this.state.prevSection}
          initialDelay={5000}
          sectionAlignment="right"
          title="About"
          content="Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy
          nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat. Ut
          wisi enim ad minim veniam, quis nostrud exerci tation ullamcor- per suscipit
          lobortis nisl ut. Lorem ipsum dolor sit amet, consectetuer adipiscing elit,
          sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat
          volutpat."
          parentStyle={{
            top: '50%',
            left: 'initial',
            right: '0%',
            //transform: 'translate(0%,-50%)',
            paddingLeft: '2rem',
            paddingRight: '2rem',
            '@media (min-width: 550px)': {
              //transform: 'translate(0%,-50%)',
              minWidth: 'auto',
              maxWidth: '400px',
              paddingLeft: '4rem',
              paddingRight: '6.3rem',
            },
          }}
        />

        <AnimatedText3
          section="Mission"
          currentSection={this.state.section}
          setHasAnimFinished={this.setHasAnimFinished}
          prevSection={this.state.prevSection}
          initialDelay={3000}
          sectionAlignment="left"
          title="Mission"
          content="Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat. Ut wisi enim ad minim veniam, quis nostrud exerci tation ullamcor- per suscipit lobortis nisl ut."
          parentStyle={{
            top: '50%',
            left: '0%',
            right: 'initial',
            //transform: 'translate(0%,-50%)',
            paddingLeft: '2rem',
            paddingRight: '2rem',
            '@media (min-width: 550px)': {
              //transform: 'translate(0%,-50%)',
              minWidth: 'auto',
              maxWidth: '400px',
              paddingLeft: '4rem',
              paddingRight: '4rem',
            },
          }}
        />

        <AnimatedForm
          setFollowDropOpen={this.props.setFollowDropOpen}
          isFollowDropOpen={this.props.isFollowDropOpen}
          setHasAnimFinished={this.setHasAnimFinished}
          section="Follow"
          initialDelay={5}
          sectionAlignment="center"
          currentSection={this.state.section}
          prevSection={this.state.prevSection}
          title="Follow"
          parentStyle={{
            top: '50%',
            left: '0%',
            right: 'initial',
            //transform: 'translate(0%,-50%)',
            paddingLeft: '2rem',
            paddingRight: '2rem',
            '@media (min-width: 550px)': {
              left: '50%',
              //transform: 'translate(-50%,-50%)',
              minWidth: '300px',
              maxWidth: '400px',
              paddingLeft: '2rem',
              paddingRight: '2rem',
            },
          }}
        />

        <AnimatedLink
          section="Join"
          initialDelay={5000}
          sectionAlignment="center"
          currentSection={this.state.section}
          setHasAnimFinished={this.setHasAnimFinished}
          prevSection={this.state.prevSection}
          title="Join & Follow"
          content="Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat."
          parentStyle={{
            top: '50%',
            left: '0%',
            right: 'initial',
            //transform: 'translate(0%,-50%)',
            paddingLeft: '2rem',
            paddingRight: '2rem',
            '@media (min-width: 550px)': {
              left: '50%',
              //transform: 'translate(-50%,-50%)',
              minWidth: '300px',
              maxWidth: '400px',
              paddingLeft: '2rem',
              paddingRight: '2rem',
            },
          }}
        />
      </React.Fragment>
    );
  }
}

export default Radium(TextHolder);
