import { createPerspectiveCamera } from './components/camera.js';
import { createRenderer } from './systems/renderer.js';
import { createScene } from './components/scene.js';
//import { createRaycaster } from './systems/raycaster.js';
import { createLights } from './components/lights.js';
import { createControls } from './systems/controls.js';

import { Loop } from './systems/Loop.js';
import { Resizer } from './systems/Resizer.js';

import { MouseListener } from './systems/MouseListener.js';
import { ProgressListener } from './systems/ProgressListener.js';
//import { PointerListener } from './systems/PointerListener.js';

import { SceneManager } from './managers/SceneManager.js';
import { CameraManager } from './managers/CameraManager.js';

import { Group, Vector3, Vector2, Euler, Quaternion} from 'three';

import * as TWEEN from '@tweenjs/tween.js';

import { loadIsland } from './components/island/island.js';
import { loadDeer } from './components/deer/deer.js';

import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js';
// import { BloomPass } from 'three/examples/jsm/postprocessing/BloomPass.js';
// import { FilmPass } from 'three/examples/jsm/postprocessing/FilmPass.js';
// import { GlitchPass } from 'three/examples/jsm/postprocessing/GlitchPass.js';

import { UnrealBloomPass } from 'three/examples/jsm/postprocessing/UnrealBloomPass.js';

// import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass.js';
// import { LuminosityShader } from 'three/examples/jsm/shaders/LuminosityShader.js';
//import { GUI } from 'three/examples/jsm/libs/dat.gui.module';
// https://threejs.org/examples/#webgl_postprocessing_unreal_bloom
const params = {
  exposure: 1,
  bloomStrength: 0.25,
  bloomThreshold: 0,
  bloomRadius: 0,
};

class World {
  constructor(
    container,
    callbackProgress,
    setCanScroll,
    section,
    setCircleStyle,
    goForward,
    goBackward,
  ) {
    this.setCanScroll = setCanScroll;
    this.setCircleStyle = setCircleStyle;
    this.goForward = goForward;
    this.goBackward = goBackward;

    this.scene = createScene();

    let hfov = -1

    if (container.clientWidth <= 450)
    {
      hfov = 50;
    }
    this.camera = createPerspectiveCamera(50, container.clientWidth/container.clientHeight, 0.01, 1000, hfov);

    const enableLogarithmic = (container.clientWidth >= 450);
    this.renderer = createRenderer(enableLogarithmic);
    //this.renderer.setSize(container.clientWidth, container.clientHeight);
    container.append(this.renderer.domElement);
    this.composer = new EffectComposer(this.renderer);
    const renderPass = new RenderPass(this.scene, this.camera);
    renderPass.renderToScreen = true;
    this.composer.addPass(renderPass);
    const bloomPass = new UnrealBloomPass(
      new Vector2(this.renderer.domElement.width, this.renderer.domElement.height),
      1.5,
      0.4,
      0.85,
    );
    bloomPass.threshold = params.bloomThreshold;
    bloomPass.strength = params.bloomStrength;
    bloomPass.radius = params.bloomRadius;
    //this.composer.addPass(this.bloomPass);

    /*const gui = new GUI();

    gui.add(params, 'exposure', 0.1, 2).onChange((value) => {
      this.renderer.toneMappingExposure = Math.pow(value, 4.0);
    });

    gui.add(params, 'bloomThreshold', 0.0, 1.0).onChange(function (value) {
      bloomPass.threshold = Number(value);
    });

    gui.add(params, 'bloomStrength', 0.0, 3.0).onChange(function (value) {
      bloomPass.strength = Number(value);
    });

    gui
      .add(params, 'bloomRadius', 0.0, 1.0)
      .step(0.01)
      .onChange(function (value) {
        bloomPass.radius = Number(value);
      });*/

    this.bloomPass = bloomPass;

    this.controls = createControls(this.camera, this.renderer.domElement);

    // Resize Canvas
    this.resizer = new Resizer(container, this.camera, this.renderer, this.composer);

    // Initialize textElements base tick method
    this.textElements = document.getElementsByClassName('th-perspective');
    this.textElements2 = document.getElementsByClassName('th-perspective2');
    for (let i = 0; i < this.textElements.length; i++) {
      this.textElements[i].tick = () => {};
    }
    for (let i = 0; i < this.textElements2.length; i++) {
      this.textElements2[i].tick = () => {};
    }

    // Ininialize Listeners
    this.mouseListener = new MouseListener(
      container,
      this.setCircleStyle,
      goForward,
      goBackward,
      this.textElements,
      this.textElements2,
    );

    this.progressListener = new ProgressListener(callbackProgress);
    //this.pointerListener = new PointerListener(container);

    this.maxPanFactor = 0.00025;

    //the camera rotation pivot
    this.orbit = new Group();
    this.orbit.position.set(0, 0, 0);
    this.orbit.rotation.order = 'YXZ';
    this.camera.rotation.order = 'YXZ';
    this.scene.add(this.orbit);
    this.orbit.add(this.camera);

    this.camera.position.set(0, 0, 0);

    // Initialize managers
    this.sceneManager = new SceneManager(this.scene);
    this.cameraManager = new CameraManager(this.camera, this.controls, TWEEN, this.orbit);

    const target = new Vector3();
    let targetQuat = new Quaternion();
    let tarQuat = new Quaternion();
    let transitionJustEnded = false;
    // let finalRot = new Vector3(0,0,0);
    // let localRotation = new Vector3(0,0,0);
    // this.orbit.tick = () => {
    //   if (!this.cameraManager.isInTransition) {
    // target.x = (1 - this.mouseListener.mouse.x) * 0.00025;
    // target.y = (1 - this.mouseListener.mouse.y) * 0.00025;
    // localRotation = this.camera.worldToLocal(this.orbit.rotation.toVector3());
    // console.log(this.orbit.rotation, localRotation);
    // let quat = new Quaternion();
    // this.camera.getWorldQuaternion(quat);
    // quat.normalize();
    // let quat2 = new Quaternion(target.y - quat.x,quat.y, quat.z, quat.w);
    // quat.invert();
    // quat2.multiplyQuaternions(quat2,quat);
    // this.orbit.rotation.setFromQuaternion(quat2);
    // console.log(this.orbit.rotation);
    //this.orbit.rotation.y += 0.05 * (target.x - this.orbit.rotation.y);
    // finalRot = this.camera.localToWorld(localRotation);
    // this.orbit.rotation.set(finalRot.x, finalRot.y, finalRot.z);
    //this.controls.target.add(this.camera.rotation);
    //this.camera.getWorldDirection(forward);
    //console.log(this.camera.rotation);
    //console.log(forward);
    //this.camera.rotation.Up
    //this.camera.rotation.crossVectors(new Vector3(0,1,0));
    //localPos = this.camera.rotation.worldToLocal(this.controls.target);
    //localPos.x += 0.05*()
    this.orbit.tick = () => {
      this.camera.rotation.z = 0;
      if (!this.cameraManager.isInTransition) {
        if (transitionJustEnded)
        {
          this.mouseListener.touch.set(0,0);
          this.mouseListener.mouse.set(0,0);
          transitionJustEnded = false;
        }
        if (container.clientWidth <= 450) {
          if (this.mouseListener.isTouching)
          {
            target.y = (- this.mouseListener.touch.x) * this.maxPanFactor*5;
            target.x = (- this.mouseListener.touch.y) * this.maxPanFactor*5;
            //console.log(this.mouseListener.touch);
            target.z = -this.camera.rotation.z;
            targetQuat = this.camera.getWorldQuaternion();
            //targetQuat.setFromEuler(this.camera.rotation);
            tarQuat.setFromEuler(new Euler(target.x, target.y, target.z, 'YXZ'))
            targetQuat.multiply(tarQuat);
            //tarQuat.setFromEuler(this.camera.rotation);
            tarQuat = this.camera.getWorldQuaternion();
            tarQuat.invert();
            targetQuat.multiply(tarQuat);
            this.orbit.rotation.setFromQuaternion(targetQuat);
          }          
          //console.log(this.camera.rotation.z, this.orbit.rotation.z);
          // target.x = (1 - this.mouseListener.touch.x) * 0.00025;
          // target.y = (1 - this.mouseListener.touch.y) * 0.00025*Math.sin(this.camera.rotation.y);
          // target.z = (1-this.mouseListener.touch.y)*0.00025*Math.cos(this.camera.rotation.y);
          // this.camera.rotation.x += 0.05 * (target.y - this.camera.rotation.x);
          // this.camera.rotation.y += 0.05 * (target.x - this.camera.rotation.y);
          // this.orbit.rotation.y += 0.05 * (target.x - this.orbit.rotation.y);
          // this.orbit.rotation.x += 0.05 * (target.y - this.orbit.rotation.x);
          // this.orbit.rotation.z += 0.05 * (target.z - this.orbit.rotation.z);
          //this.controls.update();
          // console.log(this.camera.rotation.x,',',this.camera.rotation.y,',',this.camera.rotation.z);
        } else {
          // target.x = (1 - this.mouseListener.mouse.x) * 0.00025;
          // target.y = (1 - this.mouseListener.mouse.y) * 0.00025*Math.abs(Math.cos(this.camera.rotation.y));
          // target.z = (1 - this.mouseListener.mouse.y) * 0.00025*Math.abs(Math.sin(this.camera.rotation.y));
          target.y = (- this.mouseListener.mouse.x) * this.maxPanFactor;
          target.x = (- this.mouseListener.mouse.y) * this.maxPanFactor;
          //console.log(this.mouseListener.mouse);
          target.z = -this.camera.rotation.z;
          targetQuat.setFromEuler(this.camera.rotation);
          tarQuat.setFromEuler(new Euler(target.x, target.y, target.z, 'YXZ'));
          targetQuat.multiply(tarQuat);
          tarQuat.setFromEuler(this.camera.rotation);
          tarQuat.invert();
          targetQuat.multiply(tarQuat);
          this.orbit.rotation.setFromQuaternion(targetQuat);
          // this.orbit.rotation.y += 0.05 * (target.x - this.orbit.rotation.y);
          // this.orbit.rotation.x += 0.05 * (target.y - this.orbit.rotation.x);
          // this.orbit.rotation.z += 0.05 * (target.z - this.orbit.rotation.z);
          //console.log(this.camera.rotation.y,', cosine: ',Math.cos(this.camera.rotation.y), ', sine: ',Math.sin(this.camera.rotation.y));
          // this.camera.rotation.x += 0.05 * (target.y - this.camera.rotation.x);
          // this.camera.rotation.y += 0.05 * (target.x - this.camera.rotation.y);
          // this.controls.update();
          for (let i = 0; i < this.mouseListener.textElements.length; i++) {
            this.mouseListener.textElements[i].tick();
          }
          for (let i = 0; i < this.mouseListener.textElements2.length; i++) {
            this.mouseListener.textElements2[i].tick();
          }
        }
      } else {
        this.controls.update();                
      }      
    };

    this.loop = new Loop(
      this.camera,
      this.scene,
      this.renderer,
      TWEEN,
      this.controls,
      this.composer,
    );

    this.loop.updatables.push(this.orbit);

    this.treeGroup = new Group();
    this.lightsGroup = new Group();
    this.animalGroup = new Group();
    this.islandGroup = new Group();

    this.section = '';
    this.prevSection = '';

    this.isFilterApplied = false;
  }

  init = async () => {
    const { ambientLight, pointLights } = createLights();

    await this.progressListener.initLoaders([
      'deer'
    , 'island']);

    const { deer } = await loadDeer(this.progressListener);
    const { island,
       particles1, particles2, particles3
       } = await loadIsland(
      this.progressListener,
    );

    this.loop.updatables.push(deer);

    //Particles
    particles1.tick = (delta) => {
      particles1.rotation.y += delta / 10;
      if (particles1.rotation.y >= 360)
      {
        particles1.rotation.y = 0;
      }
    };

    particles2.tick = (delta) => {
      particles2.rotation.y -= delta / 10;
      if (particles2.rotation.y >= 360)
      {
        particles2.rotation.y = 0;
      }
    };

    particles3.tick = (delta) => {
      particles3.rotation.y += delta / 10;
      if (particles3.rotation.y >= 360)
      {
        particles3.rotation.y = 0;
      }
    };

    this.loop.updatables.push(particles1);
    this.loop.updatables.push(particles2);
    this.loop.updatables.push(particles3);

    this.lightsGroup.add(pointLights);
    this.lightsGroup.add(ambientLight);
    this.animalGroup.add(deer);
    this.islandGroup.add(island);

    // this.raycaster = createRaycaster(this.mouseListener, this.camera, island);
    // this.loop.updatables.push(this.raycaster);

    // Update Controls Last To Avoid Jittering - NOT NOW - this.orbit.tick() takes care of that
    //this.loop.updatables.push(this.controls);
  };

  setSection = (section) => {
    this.prevSection = this.section;
    this.section = section;
  };

  setStart = () => {
    let newPosition = new Vector3(
      9.644615784873787,
      50.88708700592436,
      20.251849486360165,
    );

    let targetPosition = new Vector3(6, 54, 2);

    this.orbit.position.set(newPosition.x, newPosition.y, newPosition.z);
    this.controls.target.set(targetPosition.x, targetPosition.y, targetPosition.z);
    this.controls.update();
  };

  setHome = () => {
    let newPosition = new Vector3(
      9.644615784873787,
      62.88708700592436,
      20.251849486360165,
    );

    let targetPosition = new Vector3(6, 0, 2);

    const time = 5000;

    const tweenMove = this.cameraManager.moveTowards(newPosition, time);

    const tweenRotate = this.cameraManager.rotateTowards(targetPosition, time);

    tweenMove
      .onStart(() => {
        this.cameraManager.isInTransition = true;
        tweenRotate.start();

        if (!this.isFilterApplied) {
          this.bloomPass.renderToScreen = true;
          this.composer.addPass(this.bloomPass);
          this.isFilterApplied = true;
        }
      })
      .onComplete(() => {
        if (this.section === 'Home') {
          this.cameraManager.isInTransition = false;
          this.transitionJustEnded = true;
          this.maxPanFactor = 0.00005;
        }
      })
      .start();
  };

  setMission = () => {
    if (this.prevSection === 'Join') {
      let newPosition = new Vector3(-27, 0.5, -0.5);

      let targetPosition = new Vector3(7, 1, -7);

      const time = 3000;

      const tweenMove = this.cameraManager.moveTowards(newPosition, time);
      // const tweenRotate = 
      this.cameraManager.rotateTowards(targetPosition, 5000);

      tweenMove
        .easing(TWEEN.Easing.Quadratic.Out)
        .onStart(() => {
          this.cameraManager.isInTransition = true;
        })
        .onComplete(() => {
          if (this.section === 'Mission') {
            this.cameraManager.isInTransition = false;
            this.transitionJustEnded = true;
            this.maxPanFactor = 0.00025;
          }
        });

      let newPosition2 = new Vector3(
        -12.093638736995455,
        3.285632907337083,
        -9.883844131920625,
      );

      let targetPosition2 = new Vector3(35.97493, 3.039, 35.02689);

      const time2 = 2500;

      const tweenMove2 = this.cameraManager.moveTowards(newPosition2, time2);
      const tweenRotate2 = this.cameraManager.rotateTowards(targetPosition2, time2);

      tweenMove2
        .easing(TWEEN.Easing.Quadratic.In)
        .chain(tweenMove)
        .onStart(() => {
          this.cameraManager.isInTransition = true;
          tweenRotate2.easing(TWEEN.Easing.Quadratic.InOut).start();
        })
        .onComplete(() => {
          if (this.section === 'Mission') {
            this.cameraManager.isInTransition = false;
            this.transitionJustEnded = true;
          }
        })
        .start();
    } else {
      let newPosition = new Vector3(-27, 0.5, -0.5);

      let targetPosition = new Vector3(7, 1, -7);

      const time = 3000;

      const tweenMove = this.cameraManager.moveTowards(newPosition, time);
      const tweenRotate = this.cameraManager.rotateTowards(targetPosition, time);

      tweenMove
        .onStart(() => {
          this.cameraManager.isInTransition = true;
          tweenRotate.start();
        })
        .onComplete(() => {
          if (this.section === 'Mission') {
            this.cameraManager.isInTransition = false;
            this.transitionJustEnded = true;
          }
        })
        .start();
    }
  };

  setAbout = () => {
    let newPosition = new Vector3(
      -14.590381282570453,
      2.558006995730055,
      -12.8974971746831,
    );

    const time = 5000;

    let targetPosition = new Vector3(-4.8984, 5.479776, 1.20842);

    const tweenMove = this.cameraManager.moveTowards(newPosition, time);

    const tweenRotate = this.cameraManager.rotateTowards(targetPosition, time);

    tweenMove
      .onStart(() => {
        this.cameraManager.isInTransition = true;
        tweenRotate.start();
      })
      .onComplete(() => {
        if (this.section === 'About') {
          this.cameraManager.isInTransition = false;
          this.transitionJustEnded = true;
          this.maxPanFactor = 0.00025;
        }
      })
      .start();
  };

  setFollow = () => {
    let newPosition = new Vector3(
      10.093638736995455,
      3.285632907337083,
      -10.883844131920625,
    );

    let targetPosition = new Vector3(35.97493, 3.039, 35.02689);

    const time = 5000;

    const tweenMove = this.cameraManager.moveTowards(newPosition, time);

    const tweenRotate = this.cameraManager.rotateTowards(targetPosition, time);

    tweenMove
      .onStart(() => {
        this.cameraManager.isInTransition = true;
        tweenRotate.start();
      })
      .onComplete(() => {
        if (this.section === 'Follow') {
          this.cameraManager.isInTransition = false;
          this.transitionJustEnded = true;
          this.maxPanFactor = 0.00025;
        }
      })
      .start();
  };

  setJoin = () => {
    let newPosition = new Vector3(
      10.093638736995455,
      3.285632907337083,
      -10.883844131920625,
    );

    let targetPosition = new Vector3(35.97493, 3.039, 35.02689);

    const time = 2500;

    const tweenMove = this.cameraManager.moveTowards(newPosition, time);
    //const tweenRotate = 
    this.cameraManager.rotateTowards(targetPosition, 5000);

    tweenMove
      .easing(TWEEN.Easing.Quadratic.Out)
      .onStart(() => {
        this.cameraManager.isInTransition = true;
      })
      .onComplete(() => {
        if (this.section === 'Join') {
          this.cameraManager.isInTransition = false;
          this.transitionJustEnded = true;
        }
      });

    let newPosition2 = new Vector3(
      -5.093638736995455,
      3.285632907337083,
      -9.883844131920625,
    );

    let targetPosition2 = new Vector3(35.97493, 3.039, 35.02689);

    const time2 = 3000;

    const tweenMove2 = this.cameraManager.moveTowards(newPosition2, time2);
    const tweenRotate2 = this.cameraManager.rotateTowards(targetPosition2, time2);

    tweenMove2
      .easing(TWEEN.Easing.Quadratic.In)
      .chain(tweenMove)
      .onStart(() => {
        this.cameraManager.isInTransition = true;
        tweenRotate2.easing(TWEEN.Easing.Quadratic.InOut).start();
      })
      .onComplete(() => {
        if (this.section === 'Join') {
          this.cameraManager.isInTransition = false;
          this.transitionJustEnded = true;
        }
      })
      .start();
  };

  setTest = () => {
    console.log('Cam pos: ', this.camera.position);
  };

  showScene = () => {
    this.scene.add(this.treeGroup, this.islandGroup, this.lightsGroup, this.animalGroup);
  };

  render = () => {
    this.renderer.render(this.scene, this.camera);
  };

  start = () => {
    this.loop.start();
  };

  stop = () => {
    this.loop.stop();
  };

  startListeners = () => {
    this.mouseListener.start();
  };

  stopListeners = () => {
    this.mouseListener.stop();
  };

  getProgress = () => {
    return this.progressListener.getProgress();
  };
}

export { World };
