import React, { Component } from 'react';
import './FadeScreen.css';
class FadeScreen extends Component {
  state = {
    isSkipVisible: true,
    currentStyle: {
      opacity: 1,
      pointerEvents: 'auto',
    },
    isFinalState: this.props.isFinalState,
  };

  onClick = () => {
    if (!this.props.isLoading) {
      this.setState({
        isSkipVisible: false,
      });

      this.props.setLogoVideoEnded(true);
    }
  };

  componentDidUpdate() {
    if (this.state.isFinalState !== this.props.isFinalState) {
      this.setState({
        isFinalState: this.props.isFinalState,
      });
      this.setOpacity('0');
      setTimeout(() => {
        this.setPointerEvents('none');
      }, 3000);
    }
  }

  setOpacity = (op) => {
    let style = { ...this.state.currentStyle };
    style['opacity'] = op;
    this.setState({
      currentStyle: style,
    });
  };

  setPointerEvents = (pe) => {
    let style = { ...this.state.currentStyle };
    style['pointerEvents'] = pe;
    this.setState({
      currentStyle: style,
    });
  };

  render() {
    return (
      <div id="fade-screen" style={this.state.currentStyle} onClick={this.onClick}></div>
    );
  }
}

export default FadeScreen;
