import React, { Component } from 'react';
import './Navitem.css';

class Navitem extends Component {
  state = {
    isHovered: false,
  };

  onClickSection = (e) => {
    let section = e.target.id;
    this.props.changeSection(section);
  };

  getNavlinkStyle = () => {
    let navLinkStyle = {};
    if (this.props.id === this.props.hoveredItem) {
      navLinkStyle = { borderBottom: '3px solid white' };
    } else if (this.props.hoveredItem === '') {
      if (this.props.id === this.props.currentSection) {
        navLinkStyle = { borderBottom: '3px solid white' };
      }
    }

    return navLinkStyle;
  };

  onLinkEnter = (e) => {
    this.setState({ isHovered: true });
    this.props.setHoveredItem(this.props.id);
  };

  onLinkLeave = (e) => {
    this.setState({
      isHovered: false,
    });
    this.props.setHoveredItem('');
  };

  render() {
    return (
      <li className="navitem">
        <span
          className="navlink"
          id={this.props.id}
          onClick={this.onClickSection}
          style={this.getNavlinkStyle()}
          onMouseEnter={this.onLinkEnter}
          onMouseLeave={this.onLinkLeave}
        >
          {this.props.title}
        </span>
      </li>
    );
  }
}

export default Navitem;
