import React, { Component } from 'react';
import './Navbar.css';

import Navitem from '../Navitem/Navitem';

class Navbar extends Component {
  state = {
    section: '',
    navPosition: {
      start: { opacity: '0' },
      finish: { opacity: '1' },
    },
    hoveredItem: this.props.section,
  };

  componentDidUpdate() {
    if (this.state.section !== this.props.section) {
      this.setState({
        section: this.props.section,
      });
    }
  }

  getNavStyle = () => {
    return this.props.isNavVisible
      ? this.state.navPosition.finish
      : this.state.navPosition.start;
  };

  setHoveredItem = (item) => {
    this.setState({
      hoveredItem: item,
    });
  };

  render() {
    return (
      <ul className="navlist" style={this.getNavStyle()}>
        {this.props.sections.map((navItem, i) => (
          <Navitem
            key={i}
            id={navItem}
            title={navItem}
            changeSection={this.props.changeSection}
            currentSection={this.state.section}
            hoveredItem={this.state.hoveredItem}
            setHoveredItem={this.setHoveredItem}
          />
        ))}
      </ul>
    );
  }
}

export default Navbar;
