import React, { Component } from 'react';
import './Opening.css';

import Logo from '../Logo/Logo';
import Navbar from '../Navbar/Navbar';
import TextHolder from '../TextHolder/TextHolder';
import ProgressText from '../ProgressText/ProgressText';
import FadeScreen from '../FadeScreen/FadeScreen';
import Universe from '../Universe/Universe';

import LogoVideo from '../LogoVideo/LogoVideo';
import Circle from '../Circle/Circle';

import Audio from '../Audio/Audio';

import Radium from 'radium';

class Opening extends Component {
  constructor(props) {
    super(props);
    this.world = null;
    this.state = {
      prevSection: '',
      section: '',

      progress: 0,
      updateProgress: false,
      isLoading: true,

      isNavVisible: false,
      isProgressVisible: true,
      isFadeScreenVisible: true,

      logoVideoEnded: false,
      isFollowDropOpen: false,

      isFinalState: false,

      circleStyle: {
        left: '0',
        top: '0',
        width: '25px',
        height: '25px',
        transition: 'width 0.2s ease-out, height 0.2s ease-out',
      },
      isStartHovered: false,
      startWasClicked: false,
    };
  }

  componentDidMount() {}

  setStartWasClicked = (startWasClicked) => {
    this.setState({
      startWasClicked,
    });
  };

  setLoadingFinalState = (progress, isLoading, isProgressVisible) => {
    this.setState({
      progress,
      isLoading,
      isProgressVisible,
    });
  };

  setProgress = (progress) => {
    this.setState({
      progress,
    });
  };

  setIsStartHovered = (isHovered) => {
    this.setState({
      isStartHovered: isHovered,
    });
  };

  setLogoVideoEnded = (logoVideoEnded) => {
    this.setState({
      logoVideoEnded,
    });
  };

  componentDidUpdate() {
    if (this.props.section !== this.state.section) {
      this.setState(
        {
          prevSection: this.state.section,
        },
        () =>
          this.setState({
            section: this.props.section,
          }),
      );
    }

    if (!this.state.isFinalState) {
      if (this.state.logoVideoEnded && !this.state.isLoading) {
        this.setState({ isFinalState: true });
      }
    }
  }

  setFollowDropOpen = (isOpen) => {
    this.setState({
      isFollowDropOpen: isOpen,
    });
  };

  onClickGlobal = (e) => {
    if (this.state.isFollowDropOpen) {
      if (e.target.id !== 'dropbtn' && e.target.className !== 'dropitem') {
        this.setState({
          isFollowDropOpen: false,
        });
      }
    }
  };

  setCirclePointerStyle = (left, top) => {
    this.setState({
      circleStyle: {
        left,
        top,
      },
    });
  };

  onRightClick = (e) => {
    this.props.goForwardSection();
  };

  onLeftClick = (e) => {
    this.props.goBackwardSection();
  };

  getBtnStyle = () => {
    return this.state.isFinalState && this.state.section !== 'Start'
      ? {
          display: 'flex',
          '@media (min-width: 550px)': {
            display: 'none',
          },
        }
      : {
          display: 'none',
        };
  };

  render() {
    return (
      <div id="opening" onClick={this.onClickGlobal} onTouchStart={this.onClickGlobal}>
        <Logo section={this.state.section} />

        <Audio
          isFinalState={this.state.isFinalState}
          isLoading={this.state.isLoading}
          startWasClicked={this.state.startWasClicked}
        />
        <div
          className="left-button"
          onClick={this.onLeftClick}
          onTouchStart={this.onLeftClick}
          style={this.getBtnStyle()}
        >
          <span>L</span>
        </div>
        <div
          className="right-button"
          onClick={this.onRightClick}
          onTouchStart={this.onRightClick}
          style={this.getBtnStyle()}
        >
          <span>R</span>
        </div>

        <LogoVideo
          setLogoVideoEnded={this.setLogoVideoEnded}
          isFinalState={this.state.isFinalState}
          isLoading={this.state.isLoading}
        />

        <ProgressText
          isVisible={this.state.isProgressVisible}
          progress={this.state.progress}
        />

        <Navbar
          isNavVisible={this.state.isNavVisible}
          changeSection={this.props.changeSection}
          sections={this.props.sections}
          section={this.state.section}
          setDropdownOpen={this.setDropdownOpen}
        />

        <TextHolder
          prevSection={this.state.prevSection}
          section={this.state.section}
          isFinalState={this.state.isFinalState}
          setFollowDropOpen={this.setFollowDropOpen}
          isFollowDropOpen={this.state.isFollowDropOpen}
          goForwardSection={this.props.goForwardSection}
          setIsStartHovered={this.setIsStartHovered}
          isStartHovered={this.state.isStartHovered}
          setCanScroll={this.props.setCanScroll}
          width={this.props.width}
          setStartWasClicked={this.setStartWasClicked}
        />

        <FadeScreen
          isFinalState={this.state.isFinalState}
          isVisible={this.state.isFadeScreenVisible}
          progress={this.state.progress}
          setLogoVideoEnded={this.setLogoVideoEnded}
          isLoading={this.state.isLoading}
        />

        <Universe
          section={this.state.section}
          isLoading={this.state.isLoading}
          setLoadingFinalState={this.setLoadingFinalState}
          setProgress={this.setProgress}
          progress={this.state.progress}
          isFinalState={this.state.isFinalState}
          setCanScroll={this.props.setCanScroll}
          setCircleStyle={this.setCirclePointerStyle}
          goForwardSection={this.props.goForwardSection}
          goBackwardSection={this.props.goBackwardSection}
          startScrollListener={this.props.startScrollListener}
        />

        <Circle
          circleStyle={this.state.circleStyle}
          isStartHovered={this.state.isStartHovered}
        />
      </div>
    );
  }
}

export default Radium(Opening);
