class ProgressListener {
  constructor(callback) {
    this.progressData = {};
    this.callback = callback;
  }

  initLoaders = (objects) => {
    objects.forEach((object) => {
      this.progressData[object] = 0;
    });
  };

  updateProgress = (name, progress) => {
    this.progressData[name] = progress;
    this.callback();
  };

  getProgress = () => {
    let avgProgress = 0;
    let numObjects = Object.keys(this.progressData).length;
    for (const [name, progress] of Object.entries(this.progressData)) {
      avgProgress += progress;
    }

    avgProgress = Math.floor(avgProgress / numObjects);

    // Make progress just reach 99
    if (avgProgress >= 100) {
      avgProgress = 99;
    }

    return avgProgress;
  };
}

export { ProgressListener };
