import React, { Component } from 'react';
import Radium from 'radium';

import './AnimatedForm.css';

import Dropdown from '../Dropdown/Dropdown';

class AnimatedForm extends Component {
  state = {
    currentSection: '',
    stageStyles: {
      start: {
        transform: 'translate(0%,500%)', //'translate(100%,0%)',
        opacity: '0',
        transition: '',
        pointerEvents: 'none',
      },
      finish: {
        transform: 'translate(0%,0%)', //'translate(0%,0%)',
        opacity: '1',
        transition: '',
        pointerEvents: 'auto',
      },

      transitionsStart: ['transform 0.3s ease-in', ', opacity 0.3s ease-in'],
      transitionsFinish: ['transform 0.8s ease-in-out', ', opacity 0.8s ease-in-out'],
    },

    currentTitleStyle: {},
    currentFormStyle: {},

    initialDelay: this.props.initialDelay,

    startTitleDelay: 0.0 * this.props.title.split(' ').length,
    startFormDelay: 0.0,

    finishTitleDelay: 0.05 * this.props.title.split(' ').length,
    finishFormDelay: 0.6,

    countries: [
      'Colombia',
      'United States',
      'France',
      'Germany',
      'Russia',
      'Canada',
      'Australia',
    ],

    name: '',
    email: '',
    country: 'United States',

    currentStage: '',
  };

  changeCountry = (item) => {
    this.setState({
      country: item,
    });
  };

  componentDidMount() {
    /*this.setState({
      currentTitleStyle: this.state.stageStyles.start,
      currentFormStyle: this.state.stageStyles.start,
      currentStage: 'Start',
    });*/
  }

  componentDidUpdate() {
    if (this.state.initialDelay !== this.props.initialDelay) {
      this.setState({
        initialDelay: this.props.initialDelay,
      });
    }

    if (this.state.currentSection !== this.props.currentSection) {
      this.setState({
        currentSection: this.props.currentSection,
      });

      if (this.props.section === this.props.currentSection) {
        this.setState({
          currentTitleStyle: this.state.stageStyles.finish,
          currentFormStyle: this.state.stageStyles.finish,
          currentStage: 'Finish',
        });
        setTimeout(() => {
          this.props.setHasAnimFinished(true);
        }, 6650);
      } else if (this.state.currentStage !== 'Start') {
        this.setState({
          currentTitleStyle: this.state.stageStyles.start,
          currentFormStyle: this.state.stageStyles.start,
          currentStage: 'Start',
        });
      }
    }
  }

  onSubmit = (e) => {
    e.preventDefault();

    this.setState({
      name: '',
      email: '',
    });
  };

  onChangeText = (e) => {
    this.setState({
      [e.target.id]: e.target.value,
    });
  };

  getTitle = () => {
    let words = this.props.title.split(' ');
    let numberOfWords = words.length;
    const title = [];

    const transitions =
      this.state.currentStage === 'Start'
        ? this.state.stageStyles.transitionsStart
        : this.state.stageStyles.transitionsFinish;

    const titleDelay =
      this.state.currentStage === 'Start'
        ? this.state.startTitleDelay
        : this.state.finishTitleDelay;

    words.forEach((word, i) => {
      let initialDelay =
        this.state.currentStage === 'Start' ? 0 : this.state.initialDelay;
      let delay = initialDelay + (titleDelay / numberOfWords) * (i + 1);
      let titleStyle = { ...this.state.currentTitleStyle };

      transitions.forEach((transition, i) => {
        titleStyle['transition'] += transition + ' ' + delay + 's';
      });

      titleStyle['textAlign'] = this.props.sectionAlignment;
      titleStyle['pointerEvents'] = 'none';

      title.push(
        <h1 className="form-word-title" style={titleStyle} key={i}>
          {word}
        </h1>,
      );
    });

    return title;
  };

  getForm = () => {
    const titleDelay =
      this.state.currentStage === 'Start'
        ? this.state.startTitleDelay
        : this.state.finishTitleDelay;

    const formDelay =
      this.state.currentStage === 'Start'
        ? this.state.startFormDelay
        : this.state.finishFormDelay;

    const transitions =
      this.state.currentStage === 'Start'
        ? this.state.stageStyles.transitionsStart
        : this.state.stageStyles.transitionsFinish;

    const form = [];

    let formStyle = { ...this.state.currentFormStyle };
    formStyle['textAlign'] = 'left';

    let initialDelay = this.state.currentStage === 'Start' ? 0 : this.state.initialDelay;
    let delay = initialDelay + titleDelay + formDelay / 4;

    transitions.forEach((transition, i) => {
      formStyle['transition'] += transition + ' ' + delay + 's';
    });

    if (this.state.currentStage === 'Start') {
      formStyle['pointerEvents'] = 'none';
    } else {
      formStyle['pointerEvents'] = 'auto';
    }

    form.push(
      <input
        key={1}
        id="name"
        style={formStyle}
        className="form-input mb-sm"
        type="text"
        placeholder="Name"
        onChange={this.onChangeText}
        value={this.state.name}
        autoComplete="off"
        required={true}
      />,
    );

    formStyle = { ...this.state.currentFormStyle };
    formStyle['textAlign'] = 'left';
    delay += formDelay / 4;

    transitions.forEach((transition, i) => {
      formStyle['transition'] += transition + ' ' + delay + 's';
    });

    if (this.state.currentStage === 'Start') {
      formStyle['pointerEvents'] = 'none';
    } else {
      formStyle['pointerEvents'] = 'auto';
    }

    form.push(
      <input
        key={2}
        id="email"
        style={formStyle}
        className="form-input mb-sm"
        type="email"
        placeholder="Email"
        onChange={this.onChangeText}
        value={this.state.email}
        autoComplete="off"
        required={true}
      />,
    );

    formStyle = { ...this.state.currentFormStyle };
    formStyle['textAlign'] = 'left';
    delay += formDelay / 4;

    transitions.forEach((transition, i) => {
      formStyle['transition'] += transition + ' ' + delay + 's';
    });

    let btnStyle;
    if (this.state.currentStage === 'Start') {
      btnStyle = { pointerEvents: 'none' };
    } else {
      btnStyle = { pointerEvents: 'auto' };
    }

    form.push(
      <Dropdown
        key={3}
        formStyle={formStyle}
        btnStyle={btnStyle}
        currentItem={this.state.country}
        items={[
          'Colombia',
          'United States',
          'France',
          'Germany',
          'Russia',
          'Canada',
          'Australia',
        ]}
        changeItem={this.changeCountry}
        isDropdownOpen={this.props.isFollowDropOpen}
        setDropdownOpen={this.props.setFollowDropOpen}
      />,
    );

    formStyle = { ...this.state.currentFormStyle };
    formStyle['textAlign'] = 'left';
    delay += formDelay / 4;

    transitions.forEach((transition, i) => {
      formStyle['transition'] += transition + ' ' + delay + 's';
    });

    if (this.state.currentStage === 'Start') {
      formStyle['pointerEvents'] = 'none';
    } else {
      formStyle['pointerEvents'] = 'auto';
    }

    form.push(
      <input
        key={4}
        style={formStyle}
        className="form-btn"
        type="submit"
        value="Submit"
      ></input>,
    );

    return form;
  };

  render() {
    return (
      <div className="formholder th-perspective2" style={this.props.parentStyle}>
        <div className="mb-sm">{this.getTitle()}</div>
        <form className="form-follow" onSubmit={this.onSubmit}>
          {this.getForm()}
        </form>
      </div>
    );
  }
}

export default Radium(AnimatedForm);
