import { PerspectiveCamera } from 'three';

function createPerspectiveCamera(fov, aspect, near, far, hfov = -1) {
  const camera = new PerspectiveCamera(fov, aspect, near, far);
  if (hfov > 0)
  {
    camera.fov = Math.atan( Math.tan( hfov * Math.PI / 360 ) / camera.aspect ) * 360 / Math.PI; // degrees
    //camera.aspect = Math.tan(hfov*Math.PI / 360)/Math.tan(camera.fov*Math.PI / 360);
    console.log('Camera fov:',camera.fov,' aspect:',camera.aspect, ' hfov:',hfov);
    camera.updateProjectionMatrix();
  }
  else
  {
    console.log('No hfov was chosen');
  }
  return camera;
}

export { createPerspectiveCamera };
