import { MathUtils, Vector3, Quaternion } from 'three';

class CameraManager {
  constructor(camera, controls, TWEEN, orbit) {
    this.camera = camera;

    this.controls = controls;
    this.TWEEN = TWEEN;

    this.orbit = orbit;

    this.highlightObject = null;

    this.isInTransition = false;
  }

  setHightlightObject = (object) => {
    this.highlightObject = object;
    this.setControlsTarget(object);
  };

  getPivotRotation = () => {
    let position =
      this.highlightObject !== null
        ? this.highlightObject.position
        : new Vector3(0, 0, 0);
    return { x: position.x, z: position.z };
  };

  setControlsTarget = (object) => {
    this.controls.target.copy(object.position);
  };

  getRotationValues = () => {
    let highlightPosition =
      this.highlightObject !== null
        ? this.highlightObject.position
        : new Vector3(0, 0, 0);
    return {
      degrees: this.getAngle(this.camera.position, highlightPosition),
      yPos: this.getYPos(),
      radius: this.getRadius(this.camera.position, highlightPosition),
    };
  };

  getAngle = (v1, v2) => {
    let angle = Math.atan2(v1.z - v2.z, v1.x - v2.x);
    angle = angle < 0 ? Math.PI * 2 + angle : angle;
    return MathUtils.radToDeg(angle);
  };

  getRadius = (p1, p2) => {
    let v1 = new Vector3(p1.x, 0, p1.z);
    let v2 = new Vector3(p2.x, 0, p2.z);
    return v1.distanceTo(v2);
  };

  getYPos = () => this.camera.position.y;

  rotateAround = (currentValues, goalValues, duration) => {
    let center = this.getPivotRotation();
    let tweenValues = {
      degrees: currentValues.degrees,
      yPos: currentValues.yPos,
      radius: currentValues.radius,
    };

    const tweenRotateCircular = new this.TWEEN.Tween(tweenValues)
      .to(goalValues, duration)
      .easing(this.TWEEN.Easing.Quadratic.InOut)
      .onUpdate(() => {
        let theta = MathUtils.degToRad(tweenValues.degrees);
        this.camera.position.x = center.x + tweenValues.radius * Math.cos(theta);
        this.camera.position.y = tweenValues.yPos;
        this.camera.position.z = center.z + tweenValues.radius * Math.sin(theta);
      });

    return tweenRotateCircular;
  };

  moveTowards = (newPosition, moveDuration) => {
    var tweenMove = new this.TWEEN.Tween(/*this.camera.position*/ this.orbit.position)
      .to(newPosition, moveDuration)
      .easing(this.TWEEN.Easing.Quadratic.InOut);
    return tweenMove;
  };

  rotateTowards = (targetPosition, rotateDuration) => {
    var tweenRotate = new this.TWEEN.Tween(this.controls.target)
      .to(targetPosition, rotateDuration)
      .easing(this.TWEEN.Easing.Quadratic.InOut);
    return tweenRotate;
  };

  rotateTowards2 = (cameraLookAt, targetPosition, rotateDuration) => {
    /*var cameraLookAt = new Vector3(0, 0, -1);
    cameraLookAt.applyQuaternion(this.camera.quaternion);*/
    let tweenValues = {
      cameraLookAt: cameraLookAt,
    };
    var tweenRotate = new this.TWEEN.Tween(tweenValues)
      .to({ cameraLookAt: targetPosition }, rotateDuration)
      .easing(this.TWEEN.Easing.Quadratic.InOut)
      .onUpdate(() => {
        this.camera.lookAt(cameraLookAt);
      });
    return tweenRotate;
  };

  tweenRotation = (targetQuaternion, duration) => {
    let qm = new Quaternion();
    let curQuaternion = this.camera.quaternion;

    let time = { t: 0 };
    var tween = new this.TWEEN.Tween(time)
      .to({ t: 1 }, duration)
      .easing(this.TWEEN.Easing.Quadratic.InOut)
      .onUpdate(() => {
        console.log('t: ' + time.t);
        Quaternion.slerp(curQuaternion, targetQuaternion, qm, time.t);
        qm.normalize();
        this.camera.rotation.setFromQuaternion(qm);
      });

    //tween.start();
    return tween;
  };
}

export { CameraManager };
