  import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import {
  TextureLoader,
  sRGBEncoding,
  LoadingManager,
  Color,
  //Geometry,
  PointsMaterial,
  BufferGeometry,
  Float32BufferAttribute,
  Points,
  //RepeatWrapping,
  //MeshBasicMaterial,
} from 'three';

const loadIsland = async (pl) => {
  const manager = new LoadingManager();
  const loader = new GLTFLoader(manager);

  const islandPath = '/assets/island/Escena_1.2.glb';

  manager.onProgress = (item, loaded, total) => {
    if (item === islandPath) pl.updateProgress('island', (loaded / total) * 100);
  };

  manager.onLoad = () => {
    pl.updateProgress('island', 100);
  };

  const islandData = await loader.loadAsync(islandPath);

  const island = islandData.scene;

  //blue-dotted red fungus
  //console.log(island.children[3520]);

  //Load fungus emissive texture and color
  const textureLoader = new TextureLoader();
  const T_LV_FUNGUS01_000_AF = textureLoader.load(
    '/assets/island/textures/T_LV_FUNGUS01_000_AF_2.png',
  );
  T_LV_FUNGUS01_000_AF.encoding = sRGBEncoding;
  const T_LV_FUNGUS01_000_Color = new Color(0x2ba1ad);

  const T_MUSHROOM_MAGIC_AF = textureLoader.load(
    '/assets/island/textures/T_MUSHROOM_MAGIC_AF.png',
  );
  T_MUSHROOM_MAGIC_AF.encoding = sRGBEncoding;
  const T_MUSHROOM_MAGIC_Color = new Color(0xff4237);

  // const OakColor = new Color(0xad2f39);
  // const OakColor = new Color(0x5A191E);
  // const OakColor = new Color(0x36050E);
  // const OakTexture = textureLoader.load('/assets/island/textures/T_OAK_TT_001_AL.png');
  // OakTexture.encoding = sRGBEncoding;

  // const T_Water_Normal = textureLoader.load(
  //   '/assets/island/textures/waternormals.jpg',
  //   function (texture) {
  //     texture.wrapS = texture.wrapT = RepeatWrapping;
  //   },
  // );

  const T_LV_OTHERS00_AF = textureLoader.load(
    '/assets/island/textures/T_LV_OTHERS00_AF.png',
  );
  const T_LV_OTHERS00_Color = new Color(0xbabf37);
  //var waterElement = null;

  island.children.forEach((element, index) => {
    // console.log(index,": ",element.name);
    if (
      element.name === 'SM_LV_FUNGUS_002_001_N012' ||
      element.name === 'SM_LV_FUNGUS_002_001_N005'
    ) {
      //console.log(element);
      //element.color = new Color(0xffffff);
      //element.colorWrite = false;
      element.material.emissiveMap = T_LV_FUNGUS01_000_AF;
      element.material.emissive = T_LV_FUNGUS01_000_Color;
    } else if (element.name === 'SM_LV_FUNGUS_002_001_N017') {
      //console.log(element);
      element.material.emissiveMap = T_LV_FUNGUS01_000_AF;
      element.material.emissive = T_LV_FUNGUS01_000_Color;
    } else if (element.name.includes('Icosphere')) {
      // console.log(element);
      // element.material.polygonOffset = true;
      // element.material.polygonOffsetFactor = -0.1;
    } else if (element.name === 'SM_MUSHROOM_MAGIC_000012') {
      console.log(element);
      element.material.emissiveMap = T_MUSHROOM_MAGIC_AF;
      element.material.emissive = T_MUSHROOM_MAGIC_Color;
    } else if (element.name === 'SM_MUSHROOM_MAGIC_000005') {
      console.log(element.position);
    } else if(element.name === 'SM_LV_OTHERS00_002_N013') {
      element.material.emissiveMap = T_LV_OTHERS00_AF;
      element.material.emissive = T_LV_OTHERS00_Color;

    } else if (element.name === 'Sphere') {
      // console.log(element);
      // element.frustumCulled = false;
      element.material.fog = false;
      element.material.emissiveMap = element.material.map;
    }
    else if (element.name === 'Plane306') {
      //element.frustumCulled = false;
      element.material.emissive = new Color(0x363636);
      //element.material.emissiveMap = T_Lake;
      //element.material.map = T_Lake;
      //console.log(element);
    }
    // else if (element.name.includes("OAK"))
    // {
    //   element.children[1].material.emissive = OakColor;
    //   element.children[1].material.emissiveMap = OakTexture;
    //   console.log(element);
    // }
  });

  // let waterGeometry = new PlaneGeometry( 10000, 10000 );;
  // console.log(waterElement);
  // //waterGeometry = new PlaneGeometry( 10000, 10000 );
  // const water = new Water(
  //   waterElement.geometry,
  //   //waterGeometry,
  //   {
  //     textureWidth: 512,
  //     textureHeight: 512,
  //     waterNormals: T_Water_Normal,
  //     alpha: 1.0,
  //     waterColor: 0x6BB8E7,
  //     distortionScale: 3.7,
  //     fog: false
  //   }
  // );
  // water.position.set(0,0,0);
  // water.rotation.x = - Math.PI / 2;
  //island.add(water);

  //Particles

  const particleGeometry = new BufferGeometry();
  const particleGeometry2 = new BufferGeometry();
  const particleGeometry3 = new BufferGeometry();
  const vertices = [];
  const vertices2 = [];
  const vertices3 = [];

  const sprite = textureLoader.load('/assets/particle/particle3.png');

  for (let i = 0; i < 333; i++)
  {
    const x = 1000*Math.random() - 500;
    const y = 1000*Math.random() - 500;
    const z = 1000*Math.random() - 500;
    vertices.push(x,y,z);
  }
  for (let i = 0; i < 333; i++)
  {
    const x = 1000*Math.random() - 500;
    const y = 1000*Math.random() - 500;
    const z = 1000*Math.random() - 500;
    vertices2.push(x,y,z);
  }
  for (let i = 0; i < 333; i++)
  {
    const x = 1000*Math.random() - 500;
    const y = 1000*Math.random() - 500;
    const z = 1000*Math.random() - 500;
    vertices3.push(x,y,z);
  }

  particleGeometry.setAttribute('position',new Float32BufferAttribute(vertices, 3));
  particleGeometry2.setAttribute('position',new Float32BufferAttribute(vertices2, 3));
  particleGeometry3.setAttribute('position',new Float32BufferAttribute(vertices3, 3));
  const particleMaterial = new PointsMaterial({size: 2, sizeAttenuation: true, map: sprite, alphaTest:0.5, transparent: true});
  particleMaterial.depthWrite=false;
  particleMaterial.color.setHSL(1.0,0.3,0.7);

  const particles1 = new Points(particleGeometry, particleMaterial);

  const particles2 = new Points(particleGeometry2, particleMaterial);

  const particles3 = new Points(particleGeometry3, particleMaterial);
  //particles.scale.multiplyScalar(2);

  island.add(particles1);
  island.add(particles2);
  island.add(particles3);
  island.position.set(0, 0, 0);
  island.scale.multiplyScalar(0.1);
  return { island, particles1, particles2, particles3 };
};

export { loadIsland };
