import { Vector2 } from 'three';

var xDown = null;
var yDown = null;
class MouseListener {
  constructor(
    container,
    callbackCircle,
    goForward,
    goBackward,
    textElements,
    textElements2,
  ) {
    this.container = container;

    this.goForward = goForward;
    this.goBackward = goBackward;
    // For orbit rotation
    this.mouse = new Vector2();

    // For mouse position raycast
    this.mouseRaycast = new Vector2();

    //  Unused for now
    this.move = new Vector2();

    // For text perspective
    this.pointer = new Vector2();

    this.touch = new Vector2();

    //To know whether the user is touching the screen
    this.isTouching = false;

    // Callback for updating circle position in UI
    this.circle = document.getElementById('circle');
    this.callbackCircle = callbackCircle;

    // Update text elements perspective based on mouse position (pointer)
    this.constrain = 750;
    this.textElements = textElements;
    console.log(this.textElements);
    for (let i = 0; i < this.textElements.length; i++) {
      if (this.container.clientWidth <= 450) {
        this.textElements[i].style.transform = 'translate(0%,-50%)';
      }
      else{
        this.textElements[i].tick = () => {
          let x = this.pointer.x;
          let y = this.pointer.y;
  
          let calcX = -(y - this.container.clientHeight / 2) / this.constrain;
          let calcY = (x - this.container.clientWidth / 2) / this.constrain;

          this.textElements[i].style.transform =
              'perspective(500px) ' +
              '   rotateX(' +
              calcX +
              'deg) ' +
              '   rotateY(' +
              calcY +
              'deg) ' +
              ' ' +
              'translate(0%,-50%)';         
        };
      }
      
    }

    this.constrain = 750;
    this.textElements2 = textElements2;
    console.log('TEXT ELEMENTS');
    console.log(this.textElements2);
    for (let i = 0; i < this.textElements2.length; i++) {
      if (this.container.clientWidth <= 450) {
        this.textElements2[i].style.transform = 'translate(0%,-50%)';
      }
      else{
        this.textElements2[i].tick = () => {
          let x = this.pointer.x;
          let y = this.pointer.y;
  
          let calcX = -(y - this.container.clientHeight / 2) / this.constrain;
          let calcY = (x - this.container.clientWidth / 2) / this.constrain;
          this.textElements2[i].style.transform =
              'perspective(300px) ' +
              '   rotateX(' +
              calcX +
              'deg) ' +
              '   rotateY(' +
              calcY +
              'deg) ' +
              ' ' +
              'translate(-50%,-50%)';
            };
      }
      
    }
  }

  // Add mouse event listeners and link onMouseMove function
  start = () => {
    document.addEventListener('mousemove', this.onMouseMove);
    document.addEventListener('touchstart', this.handleTouchStart);
    document.addEventListener('touchend', this.handleTouchEnd);
    document.addEventListener('touchmove', this.handleTouchMove);
  };

  // Remove mouse event listener
  stop = () => {
    document.removeEventListener('mousemove', this.onMouseMove);
    document.addEventListener('touchstart', this.handleTouchStart);
    document.addEventListener('touchmove', this.handleTouchMove);
  };

  getTouches = (evt) => {
    return (
      evt.touches || // browser API
      evt.originalEvent.touches
    ); // jQuery
  };

  handleTouchStart = (evt) => {
    this.isTouching = true;
    const firstTouch = this.getTouches(evt)[0];
    xDown = firstTouch.clientX;
    yDown = firstTouch.clientY;

    this.touch.x = xDown - (this.container.clientWidth/2);
    this.touch.y = yDown - (this.container.clientHeight/2);
  };

  handleTouchMove = (evt) => {
    /*if (!xDown || !yDown) {
      return;
    }*/

    var xUp = evt.touches[0].clientX;
    var yUp = evt.touches[0].clientY;

    this.touch.x = xUp - (this.container.clientWidth/2);
    this.touch.y = yUp - (this.container.clientHeight/2);

    var xDiff = xDown - xUp;
    var yDiff = yDown - yUp;

    if (Math.abs(xDiff) > Math.abs(yDiff)) {
      /*most significant*/
      if (xDiff > 0) {
        /* left swipe */
        //this.goForward();
      } else {
        /* right swipe */
        //this.goBackward();
      }
    } else {
      if (yDiff > 0) {
        /* up swipe */
      } else {
        /* down swipe */
      }
    }
    /* reset values */
    /*xDown = null;
    yDown = null;*/
  };
  
  handleTouchEnd = (evt) => {
    this.isTouching = false;
  }

  // Each time the mouse move this happens
  onMouseMove = (e) => {
    let containerHalfX = this.container.clientWidth / 2;
    let containerHalfY = this.container.clientHeight / 2;

    this.mouse.x = e.clientX - containerHalfX;
    this.mouse.y = e.clientY - containerHalfY;

    this.pointer.x = e.clientX;
    this.pointer.y = e.clientY;

    this.circle.style.left = e.pageX + 'px';
    this.circle.style.top = e.pageY + 'px';
    this.callbackCircle(e.pageX + 'px', e.pageY + 'px');

    this.mouseRaycast.x = (e.clientX / this.container.clientWidth) * 2 - 1;
    this.mouseRaycast.y = -(e.clientY / this.container.clientHeight) * 2 + 1;

    this.move.x = e.movementX;
    this.move.y = e.movementY;
  };
}

export { MouseListener };
